.amination-r {
  animation: animation-r .25s ease-in;
}

.amination-l {
  animation: animation-l .25s ease-in;
}

.animation-icon {
  animation: house .25s ease-in;
}

@keyframes animation-r {
  0% {
    opacity: 0;
    transform: translateX(-50%);
  }

  50% {
    opacity: 1;
  }

  100% {
    transform: translateX(0);
  }
}

@keyframes animation-l {
  0% {
    opacity: 0;
    transform: translateX(50%);
  }

  50% {
    opacity: 1;
  }

  100% {
    transform: translateX(0);
  }
}

@keyframes house {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

